.App {
  text-align: center;
}

.typeAnimation-container {
  position: absolute;
  top: 5%; 
  left: 37%;
  color:  #405cf5;
  border: 0px;
  padding: 0px;
  margin: 0px;
}

.cubeman-container{
  border: 0px;
  padding: 0px;
  margin: 0px;
  position: absolute;
  left: 12%;
  top: 35%;
  width: 400px;
  height: 400px;
}

.cubemanAnimation-container{
  border: 0px;
  padding: 0px;
  margin: 0px;
  will-change: transform;
  width: 1500px;
  height: 1001px;
  background-color: aqua;
  position: absolute;
  transform: translate(-30%, -30%) scale(0.25);
  background-image: url('../public/cubeman.png');
  animation: cubeman 0.5s steps(21) infinite;
}

@keyframes cubeman{
  from {
    background-position: 0 0;
  }
  to {
    background-position: -31500px 0;
  }
}

.cubeman-button {
  border: 0px;
  padding: 0px;
  margin: 0px;
  appearance: button;
  background-color: #405cf5;
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset,rgba(50, 50, 93, .1) 0 2px 5px 0,rgba(0, 0, 0, .07) 0 1px 1px 0;
  box-sizing: border-box;
  width: 200px;
  height: 50px;
  color: #fff;
  left: 50%;
  top: 100%;
  font-family: -apple-system,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Ubuntu,sans-serif;
  font-size: 100%;
  position: relative;
  text-align: center;
}

.cubeman-button:focus {
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset, rgba(50, 50, 93, .2) 0 6px 15px 0, rgba(0, 0, 0, .1) 0 2px 2px 0, rgba(50, 151, 211, .3) 0 0 0 4px;
}

.github-button{
  border: 0px;
  padding: 0px;
  margin: 0px;
  appearance: button;
  background-color: #405cf5;
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset,rgba(50, 50, 93, .1) 0 2px 5px 0,rgba(0, 0, 0, .07) 0 1px 1px 0;
  box-sizing: border-box;
  width: 200px;
  height: 50px;
  color: #fff;
  left: 43%;
  top: 20%;
  font-family: -apple-system,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Ubuntu,sans-serif;
  font-size: 100%;
  position: absolute;
  text-align: center;
}

.github:focus {
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset, rgba(50, 50, 93, .2) 0 6px 15px 0, rgba(0, 0, 0, .1) 0 2px 2px 0, rgba(50, 151, 211, .3) 0 0 0 4px;
}

.heart-container{
  border: 0px;
  padding: 0px;
  margin: 0px;
  position: absolute;
  left: 62%;
  top: 40%;
  width: 400px;
  height: 400px;
}
.heartAnimation-container{
  border: 0px;
  padding: 0px;
  margin: 0px;
  will-change: transform;
  width: 15px;
  height: 15px;
  position: absolute;
  transform: translate(-25%, -25%) scale(15);
  left: 55%;
  top: 44%; 
  background-image: url('../public/heart-pulse.png');
  animation: heart 0.5s steps(6) infinite;
}

@keyframes heart{
  from {
    background-position: 0 0;
  }
  to {
    background-position: 90px 0;
  }
}

.heartButton{
  border: 0px;
  padding: 0px;
  margin: 10px;
  appearance: button;
  background-color: #405cf5;
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset,rgba(50, 50, 93, .1) 0 2px 5px 0,rgba(0, 0, 0, .07) 0 1px 1px 0;
  box-sizing: border-box;
  width: 200px;
  height: 50px;
  color: #fff;
  left: 30%;
  top: 75%;
  font-family: -apple-system,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Ubuntu,sans-serif;
  font-size: 100%;
  position: absolute;
  text-align: center;
}

.heartButton:focus {
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset, rgba(50, 50, 93, .2) 0 6px 15px 0, rgba(0, 0, 0, .1) 0 2px 2px 0, rgba(50, 151, 211, .3) 0 0 0 4px;
}